.admin-page {
  width: 100%;
  min-height: calc(100vh - 140px);
  padding: 10px 10px;
  border-top-left-radius: 15px;
  border-bottom-right-radius: 15px;
  background-color: #ffffff;
}
.admin-page h1 {
  font-size: 1.4rem;
}
.admin_panel {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: 0.6rem;
}
.customerbtn,
.ordersbtn,
.productsbtn {
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  position: relative;
  background-color: #333;
  color: #ffffff;
  font-size: 1rem;
  border-radius: 3px;
  font-weight: 600;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  border: none;
}
.admin_panel button:hover {
  background-color: cadetblue;
}
.counter {
  position: absolute;
  padding: 5px;
  min-width: 25px;
  right: -5px;
  top: -15px;
  background-color: #ed9121;
  color: #ffffff;
  border-radius: 3px;
  font-size: 14px;
  font-weight: 700;
}
.selected {
  background-color: cadetblue;
}
/* NOT ADMIN POP UP */
.not_admin {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  margin: 0 auto;
  padding: 1rem;
  transform: translateY(-50%);
  background-color: red;
  color: #fff;
  text-align: center;
  font-size: 1.3rem;
  font-weight: 900;
  z-index: 999;
  border: 3px solid black;
}
@media only screen and (max-width: 500px) {
  .admin-page h1 {
    font-size: 1.2rem;
  }
  .customerbtn,
  .ordersbtn,
  .productsbtn {
    padding: 7px;
    gap: 10px;
    font-size: 0.8rem;
  }
  .counter {
    padding: 3px;
    min-width: 20px;
    right: -6px;
    top: -11px;
    border-radius: 2px;
    font-size: 12px;
  }
}
