/* PRODUCT BOX */
.product-box {
  max-width: 500px;
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 20px;
  text-align: left;
  border-bottom-right-radius: 5px;
  z-index: 9;
  position: relative; /* for view-details button*/
}
/* PRODUCT NAME */
.product-title {
  text-align: center;
  font-size: 18px;
  margin-bottom: 10px;
  color: #2c0404;
}
/* PRODCUT IMAGE */
.product-image {
  width: 100%;
  height: 200px;
  object-fit: contain;
  margin-bottom: 10px;
}
/* PRODCUT PRICE */

.product-price {
  display: block;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  color: #333;
}
/* PRODCUT DESCRTIPTION */

.product-description {
  color: #666;
  margin-bottom: 10px;
}
/* VIEW DETAILS BUTTON */
.view-details-link {
  position: absolute; /* relative to product box */
  right: 5px;
  bottom: 5px;
}
.view-more-btn {
  background-color: #fff;
  border: 1px solid #ddd;
  color: black;
  outline: #333;
  padding: 5px;
  border-radius: 1px;
  font-size: 12px;
  font-weight: 500;
  transition: all 0.2s ease-out;
}
.view-more-btn:hover {
  background-color: #ed9121;
  color: white;
  border: none;
}
/* ADD TO CART BUTTON */
.add-to-cart-btn {
  display: flex;
  align-items: center;
  text-align: center;
  gap: 0.3rem;
  background-color: #333;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  padding: 5px;
  border-radius: 1px;
  border: 1px solid transparent;
  outline: none;
  margin: 5px auto;
  transition: 0.3s ease-in-out;
}
.add-to-cart-btn img {
  width: 20px;
  height: 15px;
  object-fit: contain;
}
.add-to-cart-btn:hover {
  color: #ed9121;
  background-color: #f7f7f7;
  border: 1px solid #ed9121;
}
.add-to-cart-btn:active {
  scale: 1.1;
}
/* admin mode */
.remove_product_btn {
  display: flex;
  align-items: center;
  text-align: center;
  gap: 0.3rem;
  background-color: #ff0000;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  padding: 10px;
  border-radius: 1px;
  border: 1px solid transparent;
  outline: none;
  margin: 5px auto;
  transition: 0.3s ease-in-out;
}
.remove_product_btn img {
  width: 20px;
  height: 20px;
  object-fit: contain;
}
.remove_product_btn:hover {
  color: red;
  background-color: #f7f7f7;
  border: 1px solid red;
  flex-direction: row-reverse;
}
.remove_product_btn:active {
  scale: 1.1;
}

